.ModalCartCloseButton{
    background-color: #666666; 
    border: none;
    color: white;
    text-align: center;
    font-size: 1.7rem;
    border-radius: 10px;
    -webkit-box-shadow: 18px 34px 30px -22px hsla(0,0%,0%,.2);
            box-shadow: 18px 34px 30px -22px hsla(0,0%,0%,.2);
    -webkit-transition:all .3s ease;
    -o-transition:all .3s ease;
    transition:all .3s ease;
  }
  .ModalCartCloseButton:hover{
    background-color: #2980B9;
    color: white;
    -webkit-box-shadow:2px 8px 4px -6px hsla(0,0%,0%,.3);
            box-shadow:2px 8px 4px -6px hsla(0,0%,0%,.3);
  }
  .ModalCartCheckoutButton{
    background-color: #666666; 
    border: none;
    color: white;
    text-align: center;
    font-size: 1.7rem;
    border-radius: 10px;
    -webkit-box-shadow: 18px 34px 30px -22px hsla(0,0%,0%,.2);
            box-shadow: 18px 34px 30px -22px hsla(0,0%,0%,.2);
    -webkit-transition:all .3s ease;
    -o-transition:all .3s ease;
    transition:all .3s ease;
  }
  .ModalCartCheckoutButton:hover{
    background-color: #2980B9;
    color: white;
    -webkit-box-shadow:2px 8px 4px -6px hsla(0,0%,0%,.3);
            box-shadow:2px 8px 4px -6px hsla(0,0%,0%,.3);
  }


